import { Component, OnInit, Injectable, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HomeService } from '../../../providers/homeservice';
import { TypeInfo } from '../../../UltraCreation/Core/TypeInfo';
import { TAuthService } from '../../../providers/auth';
import { NavParams } from 'ionic-angular';
import { FileService, BANKCARD_BACK, BANKCARD_FRONT } from '../../../providers/fileservice';

@Component({
    selector: 'page-adddepositcam',
    templateUrl: 'cam.html'
})
@Injectable()
export class AddDepositCamPage implements OnInit
{
  HeadTitle: string = "添加储蓄卡";
  ShowId: string;
  BankName: string = '请选择开户银行';
  BranchName: string = '请选择具体开户支行';
  BranchCode: string = '';
  TranCode: string = '';
  KeyWord: string;
  BranchList: Array<any> = [];
  Form_Group: FormGroup;
  BankCode: string = '';
  CardNo: FormControl;
  Mobile: FormControl;
  CurrentFrontCard: boolean = true;
  Previous: {} = {};
  BankCardFront: string = BANKCARD_FRONT;
  BankCardBack: string = BANKCARD_BACK;
  private defaultImg: string = 'assets/imgs/zhengm.png';
  private bankCardUploadRes: {} = {};
  App = window.App;
  @ViewChild('switchCardFace') switchEl: ElementRef;

  constructor(public Service: HomeService, public navParams: NavParams, private Auth: TAuthService, private fileService: FileService) { }

  ngOnInit()
  {
    this.Previous[BANKCARD_FRONT] = this.Previous[BANKCARD_BACK] = this.defaultImg;
    this.bankCardUploadRes[BANKCARD_FRONT] = this.bankCardUploadRes[BANKCARD_BACK] = false;
    
    if(!TypeInfo.Assigned(App.UserInfo)) {
      return;
    }
    
    this.GetIdCard(App.UserInfo['idCardNo']);
    this.Form_Group = new FormGroup({
      CardNo: this.CardNo = new FormControl('', [
        Validators.required,
        Validators.minLength(16)
      ]),

      Mobile: this.Mobile = new FormControl('', [
        Validators.required,
        Validators.pattern(/^1[3|4|5|7|8][0-9]{9}$/)
      ])
    });
  }

  DelCard()
  {
    this.Service.GetCardList().then(res => {
      for (let i = 0; i < res.length; i ++) {
        if (res[i].type === '1') {
          this.Service.DelCard(res[i].id).then(res => {
            console.log(res)
          })
        }
      }
    })
  }

  get CompleteBtnIsDisabled(): boolean
  {
    if (this.CardNo.invalid) {
      return true;
    }
    return false;
  }

  get IsUploadCardImage(): boolean
  {
    return (this.Previous[BANKCARD_FRONT] !== this.defaultImg) && (this.Previous[BANKCARD_BACK] !== this.defaultImg);
  }

  get isCompleteBankCardUpload(): boolean
  {
    for (let k in this.bankCardUploadRes) {
      if (false === this.bankCardUploadRes[k]) {
        return false;
      }
    }
    return true;
  }

  // 验证身份证
  GetIdCard(IdNo)
  {
    this.ShowId = IdNo.substr(0, 6);
    for (let i = 0; i < (IdNo.length - 10); i ++) {
      this.ShowId += '*'
    }
    this.ShowId += IdNo.substr(-4);
  }


  ClickOpenBank()
  {
    if (this.CardNo.invalid) {
      return App.ShowError('请先输入银行卡卡号');
    }
    App.ShowModal(App.RootPage.ListofbankPage).then((modal) => {
      modal.onDidDismiss(data => {
        if (data) {
          this.BankName = data.name;
          this.BankCode = data.code;
          this.BranchCode = '';
          this.BranchName = '请选择具体开户支行';
        }
      });
    })
  }

  ClickBranchBank()
  {
    if (!this.BankCode) {
      return App.ShowError('请先选择开户银行');
    }

    App.ShowModal(App.RootPage.BranchcardPage, {Bank: this.BankName, Code: this.BankCode}).then((modal) => {
      modal.onDidDismiss(data => {
        if (data) {
          this.BranchName = data.name;
          this.BranchCode = this.BankCode;
          this.TranCode = data.bankCode;
        }
      });
    })
  }

  Finish()
  {
    if (!this.isCompleteBankCardUpload) {
      App.ShowError('请先上传储蓄卡正反面!');
      return;
    }

    this.DelCard();
    this.Service.AddDeposiCard(this.Form_Group.value.CardNo, this.BankName,
      this.TranCode, this.BranchName, this.Form_Group.value.Mobile).then(res => {
        if (res) {
          App.ShowToast('储蓄卡添加成功!');
          this.Auth.GetUserData();
          App.Nav.push(App.RootPage[this.navParams.data]);
        }
      })
  }

  onClickFile(cType: string, e: any)
  {
    console.log(cType);
    this.fileService.showAddImage().then((rst: any) => {
        console.log(rst);
        this.Previous[cType] = rst.base64;
        console.log(rst.file);
        this.uploadFile(rst.file, cType);
    }).catch (error => {
        console.log(error);
    });
  }

  async uploadFile(file: File, cType: string)
  {
    let res = await this.fileService.OcrUpload('file', file, cType);

    if (false !== res) {
      this.bankCardUploadRes[cType] = true;
    }

    if (false !== res && cType === BANKCARD_FRONT) {
      this.CardNo.setValue(res.cardNo.replace(/\s*/g, ''));
      this.BankName = res.bank;
      this.BankCode = '105';
      this.switchEl.nativeElement.click();
    }
  }

  OnSwitchCard(e?: any)
  {
    this.CurrentFrontCard = !this.CurrentFrontCard;
  }
}



